.container {
  max-width: 1366px;
  padding: 0 15px;
  margin: 0 auto;
}
.header {
  background: #0dc5db;
  background: linear-gradient(to right, #0dc5db 0%, #00a8b8 60%);
  color: #FFFFFF;
  z-index: 100;
}
.header--inner {
  display: flex;
  justify-content: space-between;
}
.header--logo {
  align-items: center;
  display: flex;
  margin-left: 21.5px;
  margin-right: 80px;
}
@media screen and (min-width: 992px) {
  .header--logo {
    margin-left: 0;
  }
}
.header--logo img {
  max-height: 60px;
}
.header--logo span {
  font-family: 'Circe', Arial, sans-serif;
  font-size: 25px;
  font-weight: 100;
  line-height: 77px;
  text-transform: uppercase;
}
.header--right {
  display: flex;
}
.header--nav {
  display: none;
  flex: 1;
  text-align: center;
}
@media screen and (min-width: 992px) {
  .header--nav {
    display: flex;
  }
}
.header--search {
  align-items: center;
  cursor: pointer;
  display: none;
  justify-content: center;
  height: 77px;
  width: 77px;
}
@media screen and (min-width: 992px) {
  .header--search {
    display: flex;
  }
}
.header--search img {
  width: 18px;
}
.header--burger {
  align-items: center;
  color: #FFFFFF;
  cursor: pointer;
  display: flex;
  justify-content: center;
  height: 77px;
  outline: none;
  width: 77px;
}
@media screen and (min-width: 992px) {
  .header--burger {
    display: none;
  }
}
.header--burger img {
  width: 34px;
}
