.productItem {
  box-shadow: 0 0px 6px rgba(0, 0, 0, 0.16), 0 0px 6px rgba(0, 0, 0, 0.23);
  padding: 15px;
}
@media screen and (min-width: 768px) {
  .productItem {
    display: flex;
    justify-content: space-between;
  }
}
.productItemDescription {
  text-align: right;
}
.productItemDescriptionName {
  font-size: 30px;
}
.productItemDescriptionPrice {
  font-size: 30px;
}
