.mainnav {
  align-items: center;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
.mainnav--parentli {
  position: relative;
}
.mainnav--parentli--link {
  border-radius: 5px;
  display: inline-block;
  font-size: 16px;
  line-height: 29px;
  padding: 0 10px;
  color: #FFFFFF;
  text-decoration: none;
  text-transform: uppercase;
  outline: none;
}
.mainnav--parentli--link:hover,
.mainnav--parentli--link:active,
.mainnav--parentli--linkfocus,
.mainnav--parentli--link:visited {
  color: #FFFFFF;
}
.mainnav--parentli--link:hover,
.mainnav--parentli--link:focus {
  background-color: #ff415c;
}
.mainnav--parentli:hover .mainnav--inner {
  display: block;
}
.mainnav--inner {
  background-color: rgba(238, 239, 240, 0.76);
  border-radius: 5px;
  display: none;
  left: 0;
  list-style: none;
  margin: 0;
  padding: 10px 0;
  position: absolute;
  top: 100%;
  z-index: 10;
}
.mainnav--inner li {
  padding: 0 29px;
  text-align: left;
}
.mainnav--inner li a {
  color: #777777;
  font-size: 14px;
  line-height: 30px;
  white-space: nowrap;
}
.mainnav--inner li a:hover {
  background: inherit;
  color: #ff415c;
}
