.footer {
  background: linear-gradient(to right, #0dc5db 0%, #00a8b8 60%);
  color: #FFFFFF;
  margin-top: 30px;
}
.container {
  box-sizing: border-box;
  max-width: 1366px;
  padding: 0 15px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding: 40px 0;
}
@media screen and (min-width: 1200px) {
  .container {
    flex-direction: row;
    padding: 40px 15px;
  }
}
.socials {
  display: flex;
  justify-content: space-around;
  padding-bottom: 30px;
}
@media screen and (min-width: 1200px) {
  .socials {
    padding: 0;
  }
}
.socials a {
  padding: 10px;
}
.contacts {
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
}
@media screen and (min-width: 768px) {
  .contacts {
    flex-direction: row;
  }
}
@media screen and (min-width: 1200px) {
  .contacts {
    padding: 0;
  }
}
.address,
.phone,
.email {
  align-items: center;
  font-family: 'Circe', Arial, sans-serif;
  font-size: 12px;
  display: flex;
  padding: 20px 0;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .address,
  .phone,
  .email {
    flex-direction: row;
    padding: 20px;
  }
}
.address img,
.phone img,
.email img {
  height: 38px;
  width: 38px;
  margin-right: 10px;
}
.address div a,
.phone div a,
.email div a,
.address div a:visited,
.phone div a:visited,
.email div a:visited {
  color: #FFFFFF;
  text-decoration: none;
}
.address div p,
.phone div p,
.email div p {
  margin: 0;
}
